      input.search-input(type="text" placeholder="Please enter the words"  @keyup.enter="search" v-model="searchWord" maxlength="30" required)

</template>

<style lang="less">

  @import '../../assets/variable.less';

  #search-container{
    position: absolute;
    top: 10%;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    width: 90%;
    height: 90%;

    .search {
      width: 100%;
      height: 100%;
      text-align: center;
      text-transform: uppercase;

      .search-input {
        padding: 1rem 1.6rem;
        width: 80%;
        font-size: 1.2rem;
        color: @black;
        border: 3px solid white;
        border-radius: 5px;
        line-height: 1em;
      }

      .info {
        color: #fff;
        letter-spacing: 0.05em;
        opacity: 0.8;
      }
    }
